@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;600&family=Raleway&display=swap);
/* 
font-family: 'Quicksand', sans-serif;
font-family: 'Raleway', sans-serif;
  */

html {
  scroll-behavior: smooth;
  background-color: #222222;
}

body {
  margin: 0;
  font-family: "Quicksand", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  max-width: "100%";
}

#root {
  background-color: #222222;
  color: white;
}

.navBar {
  padding-top: 7vh;
  padding-bottom: 5vh;
}

.navLink,
.navLink:hover {
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 600;
  color: white;
  margin: 40px;
  text-decoration: none;
}

/* first section height compensate navbar */
.sectionOne {
  height: 100vh;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  /* justify-content: center; */
  -webkit-flex-direction: column;
          flex-direction: column;
}

.quoteSection {
  -webkit-justify-content: center;
          justify-content: center;
}

.section {
  /* height: 50vh; */
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.heading {
  font-family: "Raleway", sans-serif;
  font-size: 70px;
}

.sectionHeading {
  font-family: "Raleway", sans-serif;
  font-size: 5.9vh;
  /* color: grey;  */
  text-align: left;
}

.sectionContent {
  font-family: "Quicksand", sans-serif;
  font-size: 2vmin;
  /* color: grey;  */
  text-align: left;
}

.Loading {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #444;
  border-radius: 50%;
  width: 20vh;
  height: 20vh;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Feed {
  margin: auto;
  padding: 2% 1%;
}

.Feed a img {
  margin: 1% 1%;
  width: 23%;
  float: left;
  -webkit-animation: fadein 3s;
          animation: fadein 3s;
  border-radius: 10px;
}

.heading {
  height: 35vh;
  font-size: 4rem;
}

.img-fluid {
  float: right !important;
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.rowVolume {
  max-width: 100%;
  display: -webkit-flex !important;
  display: flex !important;
  padding: 0px 20px;
}

.videoSection {
  height: "70vh" !important;
  padding: "15vh 0 15vh 0" !important;
}

.img-fluid {
  float: right !important;
}

.footer {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  bottom: 0;
}

.MediaSection {
  display: block;
}

.initiative {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  height: 80vh;
}
.imageSpacerImageSection {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.ImageSectionRow {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
}
.ImageSectionContent {
  -webkit-flex: 2 1;
          flex: 2 1;
}
.ImageSectionHeading {
  width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

@media screen and (max-width: 1000px) {
  .navBar {
    padding-top: 2vh;
    padding-bottom: 2vh;
  }

  .row {
    margin-left: 0px;
  }

  .heading {
    font-size: 3rem;
    text-align: center;
  }
}

@media screen and (max-width: 580px) {
  .row {
    margin-left: 0px !important;
  }

  .heading {
    font-size: 2rem !important;
    text-align: center;
  }

  .img-fluid {
    margin: auto;
    width: 80% !important;
    float: none !important;
  }
  .react-reveal {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
  .fontSection {
    font-size: 1rem;
  }

  .footer {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .ImageSectionRow {
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
  }
  .ImageSectionHeading h2 {
    text-align: center;
  }
  .initiative {
    height: auto !important;
  }
}

